body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
  display: block;
}

button {
  cursor: pointer;
}

img {
  width: auto;
  height: auto;
  display: block;
}

body.modal-open {
  overflow: hidden;
}

:root {
  --main-backgraund-color: #f3f3f3;
  --main-white-color: #fff;
  --main-black-color: #111;
  --secondary-black-color: #202024;
  --blue-color: #4f2ee8;
  --yellow-color: #eac645;
  --cubic: cubic-bezier(.53, .36, .67, 1.07);
}

body {
  color: var(--main-black-color);
  background-color: #b4afaf;
  font-family: DM Sans;
}

.dark-theme {
  background-color: var(--secondary-black-color);
  color: #b4afaf;
}

span {
  color: var(--blue-color);
}

.container {
  max-width: 335px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 704px;
    padding-left: 32px;
    padding-right: 32px;
    overflow: hidden;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1392px;
    padding-left: 24px;
    padding-right: 24px;
    overflow: visible;
  }
}

.support-title {
  color: var(--main-white-color);
  letter-spacing: -1.12px;
  text-align: center;
  font-size: 28px;
  line-height: 1.14;
}

.support-box {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  width: 335px;
  height: 352px;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}



